<template >
  <div class="upload-button" align="center">
    <input
    :style="{'font-size':touchSize+'rem'}"
    type="file"
    value=""
    accept="image/png,image/jpg,image/gif,image/jpeg"
    :multiple=multiple
    @change='bindchange_chooseImages'
    />
  </div>
</template>

<script>

import lrz from 'lrz';

export default {

  name:'image-upload',

  props:{
    // 
    fieldName:'',
    // 
    url:'',
    // 
    multiple:{
      type:Boolean,
      default:false
    },
    // 
    multiUpload:{
      type:Boolean,
      default:false,
    },
    // 
    touchSize:{
      type:Number,
      default:0.5
    },
    // 
    lrzOptions:{
      type:Object,
      default:function(){
        return {quality:0.7};
      }
    },
    // 
    data:{
      type:Object,
      default:function(){
        return {};
      }
    },
    // 
    maxCount:{
      type:Number,
      default:-1,//-1
    }

  },

  methods:{
    /**
     *   -- 
     */
    bindchange_chooseImages(e){

      if (this.maxCount>=0 && e.target.files.length>this.maxCount) {
        return this.$emit('chooseImages','');
      }

      this.parseImages(e.target.files)
      .then(res => {
        this.$emit('chooseImages',res);
      }).catch(e => {
        console.log(e);
      })
    },

    /**
     *  
     */
    uploadImages(images){
      if (this.multiUpload) {

      }else {
        let promises = [];
        images.forEach((v , i) => {
          if (!v.upload) {
            promises.push(this.uploadSingleImage(v.file));
          }
        })
        return Promise.all(promises)
      }

    },

    /**
     *  
     *  TODO 
     */
    uploadMultipleImages(){

    },

    /**
     *  
     *
     *  imagefile
     */
    uploadSingleImage(image){
      return new Promise((resolve , reject) => {
        var formData = new FormData();
        formData.append(this.fieldName, image);
        formData.append('type', 'image');
        $.ajax({
                  url: this.url,
                  type: 'POST',
                  dataType: 'JSON',
                  cache: false,
                  data: formData,
                  processData: false,
                  contentType: false,
                  success: (res) => {
                    resolve(res);
                  },
                  error: function(err) {
                    reject(err);
                  }
              });
      })
    },


    /**
     *  
     */
    parseImages(files){
      if (this.lrzOptions) {
        return this.compressImages(files);
      }else {
        return this.readImages(files);
      }
    },

    /**
     *  
     */
    compressImages(files){
      let promises = [];
      for (var i = 0; i < files.length; i++) {
          promises.push(lrz(files[i],{width: 1024}));
      }
      return new Promise((resolve , reject) => {
        let images = [];
        Promise.all(promises)
        .then(res => {
          res.forEach(v => {
            images.push({file:v.file, src:v.base64, compress:true});
          })
          resolve(images);
        }).catch(e => {
          reject(e);
        })
      })
    },

    /**
     *  
     */
    readImages(files){
      return new Promise((resolve , reject) => {
        let images = [];
        for (var i = 0; i < files.length; i++) {
          let reader = new FileReader();
          !function (j) {
            reader.onload = function (e) {
              images[j] = {file:files[j] , src:e.target.result , compress:false};
              if (j === files.length-1) {
                resolve(images);
              }
            };
          }(i);
          reader.readAsDataURL(files[i]);
        }
      })
    },


  },
}
</script>

<style lang="css">
.upload-button{position: relative;display: inline-block;overflow: hidden;}
.upload-button input{position:absolute;left: 0rem; top: 0rem;opacity: 0.0;-ms-filter: 'alpha(opacity=0)';font-size: 0.5rem;}
</style>



